    tradeItem.image = "";
    tradeItem.money = undefined;
    tradeItem.create_time = "";
    tradeItem.status = undefined;
    tradeItem.desc = "";
    tradeItem.product_type_id = undefined;
}

const assignmentTradeItem = (item: tradeInterFace) => {
    tradeItem.id = item.id.toString();
    tradeItem.name = item.name;
    tradeItem.image = item.image;
    tradeItem.money = item.money / 100;
    tradeItem.create_time = item.create_time;
    tradeItem.status = item.status
    tradeItem.desc = item.desc;
    tradeItem.product_type_id = item.product_type_id;

