    tradeItem.image = response.msg;
}


//查询全部
const produFindAll = async () => {
    table.loading = true;
    const { code, data: { rows, count } } = await queryprodu(productQuery.productTypeId, productQuery.index, productQuery.tradeName, productQuery.status)
    if (code == 200) {
        table.loading = false;
        productList.value = rows;
        total.value = count;
    }
}

//获取商品分类
const produType = async () => {
    const { code, data } = await queryproduType(0)
    if (code == 200) {
        titleList.value = data;
        titleList.value.unshift({
            id: "",
            label: "全部商品",
            create_time: "",
            status: undefined,
        })
    }
}


//点击使用添加商品
const addShowDoaiog = () => {
    showDoaiog.show = true;
    showDoaiog.title = "添加商品";
    showDoaiog.isAdd = true;
    clearTradeItem();

}
//点击编辑商品
const changeShowDoaiog = (item: tradeInterFace) => {
    showDoaiog.show = true;
    showDoaiog.title = "编辑商品";
    showDoaiog.isAdd = false;
    assignmentTradeItem(item);
}



const funUpdateTrade = async (formEl: FormInstance | undefined) => {

    if (!formEl) return
    await formEl.validate(async (valid, fields) => {
        if (valid) {
            const { msg, code } = await updateTrade(tradeItem.id, tradeItem.name, tradeItem.desc, tradeItem.image, tradeItem.money, tradeItem.status, tradeItem.product_type_id)
            if (code === 200) {
                showDoaiog.show = false;
                produFindAll();
                ElMessage.success(msg);
            }

        } else {
            console.log('error submit!', fields)
        }
    })

}


const funAddTrade = async (formEl: FormInstance | undefined) => {

    if (!formEl) return
    await formEl.validate(async (valid, fields) => {
        if (valid) {
            const { msg, code } = await addTrade(tradeItem.name, tradeItem.desc, tradeItem.image, tradeItem.money, tradeItem.status, tradeItem.product_type_id)
            if (code == 200) {
                showDoaiog.show = false;
                produFindAll();
                ElMessage.success(msg);
            }

        } else {
            console.log('error submit!', fields)
        }
    })

}

